import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SelectItem, Message, ConfirmDialogModule, ConfirmationService, AutoCompleteModule } from 'primeng/primeng';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import * as _ from 'lodash';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'create-customGroup-admin',
    templateUrl: './create-customGroup-admin.component.html',
    styleUrls: ['./create-customGroup-admin.component.scss'],
    providers: [CusOperationHttpService, Commfunc]
})
export class CreateCustomGroupAdminComponent implements OnInit {
    @Output() event: EventEmitter<any> = new EventEmitter<any>();
    @Input() isContactPerson: any;
    @Input() inValue: any;
    @Input() adminFlag: any;

    //
    tableMesg: any = tableMessage;
    // 
    outValue: any = [];
    // 
    addDisplay: boolean = false;
    staff_display: boolean = false;//
    staff_info: any;
    // 
    groupZone: any = '';
    Property: boolean = false;
    // 
    staff: any = '';
    staffArr: any = [
        { label: '', value: ['', ""] },
        { label: '1', value: ['1', "1"] },
        { label: '2', value: ['2', "2"] },
        { label: '3', value: ['3', "3"] },
        { label: '4', value: ['4', "4"] },
        { label: '5', value: ['5', "5"] },
        { label: '6', value: ['6', "6"] },
        { label: '7', value: ['7', "7"] },
        { label: '8', value: ['8', "8"] },
        { label: '9', value: ['9', "9"] },
    ];
    userIdTemp: any = this.staffArr[0].value[0];
    userNameTemp: any = this.staffArr[0].value[1];
    data: any;//

    // 
    msgs: Message[] = [];
    // 
    filteredCountriesSingle: any[];
    filteredCountriesMultiple: any[];
    brands: string[] = ['Audi', 'BMW', 'Fiat', 'Ford', 'Honda', 'Jaguar', 'Mercedes', 'Renault', 'Volvo', 'VW'];
    filteredBrands: any[];
    brand: string;
    /**
     * 2018.1.9--
     */
    // session
    ownerId: any = '';
    ownerName: any = '';
    // 
    hasFlag: any = 0;//
    concatFlag: any = 0;//
    // invalue
    oldValue: any = [];
    /**
     *--2018.1.9
     */

    constructor(
        public httpService: CusOperationHttpService,
        public router: Router,
        public routerInfo: ActivatedRoute,
        public commfunc: Commfunc,
    ) { }
    ngOnInit() {
        this.preSet();
    }
    preSet() {
        let session = window.sessionStorage;
        let data = session.getItem('commonHeader');
        if (!data) {
            this.router.navigate(["/login"]);
            return;
        }
        let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (!mesg) {
        }
        let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.ownerId = mesg['userId']//id
        this.ownerName = ch['userName'];//
    }
    delArray(i) {
        // 
        this.inValue.splice(i, 1);
        this.inValue = _.clone(this.inValue);
    }
    return() {
        // 

        this.inValue = _.clone(this.inValue);
        this.event.emit({
            adminDisplay: false,
            outValue: this.inValue,
            adminFlag: this.adminFlag,
        })
    }
    // 
    changeStaff(event) {
        // 

        // 
        this.userIdTemp = event.value[0];
        this.userNameTemp = event.value[1];
    }
    addBtn() {
        // 

        // 
        let tmpFlag1 = true;
        tmpFlag1 = this.isDisable();
        if (!tmpFlag1) {
            return;
        }

        // 
        let tmpFlag3 = true;
        tmpFlag3 = this.isHave();
        if (!tmpFlag3) {
            this.addDisplay = false;
            return;
        }

        // 
        this.inValue.push(
            {
                groupZone: this.groupZone,
                userId: this.userIdTemp,
                userName: this.userNameTemp,
                statusId: '',
            }
        )
        this.inValue = _.clone(this.inValue);
        // 
        this.staff = '';
        this.groupZone = '';
        this.userIdTemp = this.staffArr[0].value[0];
        this.userNameTemp = this.staffArr[0].value[1];
        // 
        this.addDisplay = false;
    }

    isContact() {
        // 
        if (!this.isContactPerson) {
            if (this.inValue.length >= 1) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: ',!' }];
                return false;
            }
        }
        return true;
    }
    isHave() {
        // 
        for (let i in this.inValue) {
            if (this.inValue[i].userId == this.userIdTemp && this.inValue[i].userName == this.userNameTemp) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
                return false;
            }
        }
        return true;
    }
    isDisable() {
        // 

        if (!this.userIdTemp || this.userIdTemp == '') {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
            return false;
        }
        if (this.isContactPerson && (!this.groupZone || this.groupZone == '')) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
            return false;
        }
        return true;
    }

    // 
    filterBrands(event) {
        this.filteredBrands = [];
        for (let i = 0; i < this.brands.length; i++) {
            let brand = this.brands[i];
            if (brand.toLowerCase().indexOf(event.query.toLowerCase()) == 0) {
                this.filteredBrands.push(brand);
            }
        }
    }
    returnInfo(data) {
        /**
         * 2018.1.9
         */
        if (data.length > 1) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
            this.hasFlag = 1;
            return;
        }
        else {
            this.hasFlag = 0;
        }
        console.log(data);
        let stu = this.inValue.every(item => {
            let stu_2 = data.every(item_2 => {
                return item_2.tellerId != item.userId
            });
            return stu_2;
        })
        if (stu) {
            // data.forEach(item_3 => {
            //     this.inValue.push({
            //         userId: item_3.tellerId,
            //         userName: item_3.tellerName,
            //     })
            // });
        } else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return;
        }
        if (this.isContactPerson && data[0].tellerId === this.ownerId && data[0].tellerName === this.ownerName) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
            this.concatFlag = 1;
            return;
        }
        else {
            this.concatFlag = 0;
        }
        /**
         * 2018.1.9
         */
        this.data = data;
        this.staff_info = data[0].tellerName;
        this.staff = [data[0].tellerId, data[0].tellerName];
        this.staff_display = false;

        this.addBtn2();//2018.1.9
    }
    staffClick() {
        this.staff_display = true;
    }
    // 
    showDialog() {
        /**
        * 2018.1.9
        */
        this.staff_display = true;
        this.oldValue = _.clone(this.inValue);
        /**
         * 2018.1.9
         */
        this.Property = true;
        if (this.adminFlag == 1) {
            // 
            // 
            let tmpFlag2 = true;
            tmpFlag2 = this.isContact();
            if (!tmpFlag2) {
                this.addDisplay = false;
                return;
            }
            //         
            this.staff = '';
            this.groupZone = '';
            this.userIdTemp = this.staffArr[0].value[0];
            this.userNameTemp = this.staffArr[0].value[1];
            // 
            this.addDisplay = true;
        } else {
            this.staffClick()
        }
        this.addBtn2();
    }
    addBtn2() {
        /**
         * 2018.1.9
        */
        this.staff_info = '';
        if (!this.staff || this.staff.length === 0) {
            this.addDisplay = false;
            return;
        }
        /**
         * 2018.1.9
         */
        if (this.adminFlag == 1 || this.adminFlag == 5) {

        }
        if (this.adminFlag == 1) {
            if (this.inValue.length > 5) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            } else {
                this.inValue.push({
                    userId: this.staff[0],
                    userName: this.staff[1],
                    groupZone: this.groupZone
                })
                this.inValue = _.clone(this.inValue)
            }

        } else if (this.adminFlag != 5 && this.adminFlag != 3) {
            let stu = this.inValue.every(item => {
                this.data.forEach(item_2 => {
                    return item_2.userId != item.userId
                });

            })
            if (stu) {
                this.data.forEach(item_3 => {
                    this.inValue.push({
                        userId: item_3.tellerId,
                        userName: item_3.tellerName,
                        groupZone: this.groupZone
                    })
                });
                this.inValue = _.clone(this.inValue)
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            }

        } else {
            if (this.inValue.length > 0) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            } else {
                let stu = this.inValue.every(item => {
                    return this.staff[0] != item.userId
                })
                if (stu) {
                    this.inValue.push({
                        userId: this.staff[0],
                        userName: this.staff[1],
                    })
                    this.inValue = _.clone(this.inValue)
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                }
            }
        }

        this.addDisplay = false;
    }
    returnInfoHide() {
        /**
         * 2018.1.9
        */
        // this.addBtn2();
        /**
         * 2018.1.9
         */
        // this.Property = false;
    }
}